const User = [{userid:1,username:'demo20220316@gmail.com',password:'abcd1234',name:'Demo'},
                {userid:2,username:'t@gmail.com',password:'abcd1234',name:'Temo'}]

function tempLoginAccount(type,username,password,name){
    if(type==='GET'){
        return User
    } else if (type==='add') {
        User.push({userid:Date.now(), username:username, password:password, name:name})
        return User;
    }
    console.log(User)
}


export default tempLoginAccount